import { mapMutations } from 'vuex';
import Api from '@/common/api';
export default {
	data() {
		return {
			mobile: '',
			password: '',
			confimpassword: '',
			parnetId: 0,
			logining: false
		};
	},
	onLoad(ops) {
		if (ops.parentId) this.parnetId = ops.parentId;
	},
	methods: {
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.switchTab({
				url: '/pages/index/index'
			});
		},
		toRegist() {
			uni.navigateTo({
				url: '/pages/public/login'
			});
		},
		async reg() {
			let phoneReg = /^1[1-9][0-9]\d{8}$/;
			try {
				if (this.mobile == '') {
					throw '请填写手机号';
				}
				if (!phoneReg.test(this.mobile)) {
					throw '手机号格式有误';
				}
				if (!this.password) {
					throw '请填写密码';
				}
				if (this.password != this.confimpassword) {
					throw '密码不一致';
				}
			} catch (err) {
				this.$api.msg(err);
				return;
			}

			this.logining = true;
			let parentId = this.parnetId;
			let params = { phone: this.mobile, password: this.password, parentId: parentId };
			let data = await Api.apiCall('post', Api.member.reg, params);
			this.logining = false;
			if (data.code === 0) {
				this.$common.modelShow(
					'线下支付说明',
					'请联系客服进行线下支付qq:951449465',
					() => {
						uni.navigateTo({
							url: '/pages/public/login'
						});
					},
					false,
					'确定'
				);
			} else {
				this.$api.msg('注册失败');
			}
		}
	}
};
